<template>
  <div class="hello">
    <div class="row">
      <div class="col s12 m6 offset-m3">
        <div :class="'card horizontal animated ' + (index%2==0 ? 'fadeInUp' : 'fadeInDown')" v-for="(chapter, index) in borda.chapters">
          <div class="card-image">
            <ul class="left clear no-margin smaller-font share-list">
              <li>
                <a :href="'https://www.facebook.com/dialog/share?app_id=1911435875540295&display=popup&href=' +  get_current_path() + '/chapter/' + index" >
                  <i class="fa fa-facebook-square" aria-hidden="true"></i>
                </a>
              </li>
              <li><a class="twitter-share-button"
                     :href="'https://twitter.com/intent/tweet?text=' + '&via=bordaelmadyh&hashtags=البردة&url='+  get_current_path() + '/chapter/' + index"
                     target="_blank">
                <i class="fa fa-twitter-square" aria-hidden="true"></i>
              </a></li>
            </ul>
          </div>
          <div class="card-stacked">
            <div class="card-content">
              <span class="card-title normal-font" v-on:click="openChapter(index)"> <img src="../assets/book.png" class="book-icon" alt="">{{chapter.desc}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <ul>
      <li><a href="https://www.facebook.com/bordaelmadyh/" target="_blank">تابعنا علي فيس بوك</a></li>
      <li><a href="https://twitter.com/bordaelmadyh" target="_blank">تابعنا علي تويتر</a></li>
      <li><a href="https://play.google.com/store/apps/details?id=com.ionicframework.borda215096" target="_blank">تحميل تطبيق اندرويد</a>
      </li>
    </ul>
  </div>
</template>

<script>
  import Bordas from '../Bordas'

  export default {
    name: 'ViewBorda',
    data () {
      return {
        borda: Bordas.Bordas[this.$route.params.borda_index]
      }
    },
    methods: {
      openChapter: function (index) {
        this.$router.push({
          name: 'ChapterDetails',
          params: {borda_index: this.$route.params.borda_index, chapter_index: index}
        })
      },
      get_current_path: function () {
        return document.URL
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



