<template>
  <div class="row">
    <div class="col s12 m6 offset-m3">
      <div class="card horizontal animated fadeInUp">
        <div class="card-image">
          <ul class="left clear no-margin smaller-font share-list">
            <li>
              <a :href="'https://www.facebook.com/dialog/share?app_id=1911435875540295&display=popup&href=' + get_current_path()" >
                <i class="fa fa-facebook-square" aria-hidden="true"></i>
              </a>
            </li>
            <li><a class="twitter-share-button"
                   :href="'https://twitter.com/intent/tweet?text=' + line.right + '  **  ' +  line.left + '&via=bordaelmadyh&hashtags=البردة&url=' + get_current_path()"
                   target="_blank">
              <i class="fa fa-twitter-square" aria-hidden="true"></i>
            </a></li>
          </ul>
        </div>
        <div class="card-stacked">
          <div class="card-content">
            <p class="left">{{line.right}}</p>
            <br>
            <p class="right"> {{ line.left }}  <span v-show="line.count" class="count"> line.count </span> </p>
            <br class="clear">
            <br>

          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Bordas from '../Bordas'
  export default {
    name: 'SingleLine',
    data () {
      return {
        line: Bordas.Bordas[this.$route.params.borda_index]['chapters'][this.$route.params.chapter_index]['lines'][this.$route.params.line_index]
      }
    },
    methods: {
      get_current_path: function () {
        return document.URL
      }
    }
  }
</script>
<style>
</style>



