<template>
  <div class="row">
    <div class="col s12 m6 offset-m3">
      <div :class="'card horizontal animated ' + (index%2==0 ? 'fadeInUp' : 'fadeInDown')" v-for="(borda, index) in Bordas">
        <div class="card-image">
          <ul class="left clear no-margin smaller-font share-list">
            <li>
              <a :href="'https://www.facebook.com/dialog/share?app_id=1911435875540295&display=popup&href=' + get_current_path() + 'borda/' + index" >
                <i class="fa fa-facebook-square" aria-hidden="true"></i>
              </a>
            </li>
            <li><a class="twitter-share-button"
                   :href="'https://twitter.com/intent/tweet?text=' + '&via=bordaelmadyh&hashtags=البردة&url'+ get_current_path() + 'borda/' + index"
                   target="_blank">
              <i class="fa fa-twitter-square" aria-hidden="true"></i>
            </a></li>
          </ul>
        </div>
        <div class="card-stacked">
          <div class="card-content">
               <!--@mouseover="show_action(borda)" @mouseleave="hide_action(borda)" >-->
            <span class="card-title bigger-font" v-on:click="openBorda(index)"> <img src="../assets/book.png"
                                                                                     class="book-icon"
                                                                                     alt="">{{borda.author}}</span>
            <span class="normal-font">{{borda.desc}}</span>
            <p class="clear"></p>
            <br>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script scoped>
  import Bordas from '../Bordas'
export default {
    name: 'Home',
    data () {
      return {
        Bordas: Bordas.Bordas
      }
    },
    methods: {
      openBorda: function (index) {
        this.$router.push({name: 'ViewBorda', params: {borda_index: index}})
      },
      get_current_path: function () {
        return document.URL
      }
//      ,
//      show_action: function (item) {
//        item.customClass = ' animated fadeIn'
//      },
//      hide_action: function (item) {
//        item.customClass = ' hide'
//      }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



